<script>
const { JX3BOX } = require("@jx3box/jx3box-common");

export default {
    name: "Info",
    props: [],
    data: function() {
        return {
            active_rank_type: "11",
            rank_types: [
                { sub: "11", name: "秘境" },
                { sub: "40", name: "家园" },
                { sub: "7", name: "任务" },
                { sub: "5", name: "技艺" },
                { sub: "9", name: "战斗" },
                { sub: "6", name: "阅读" },
                { sub: "16", name: "活动" },
                { sub: "8", name: "足迹" },
                { sub: "15", name: "节日" },
            ],
            ranks: null,
            groups: null,
        };
    },
    computed: {},
    methods: {
        author_url(user_id) {
            return `${JX3BOX.__Links.author}${user_id}`;
        },
        copy_success() {
            this.$notify({ title: "复制成功", type: "success" });
        },
        copy_error() {
            this.$notify({ title: "浏览器不支持", type: "error" });
        },
        get_users_ranks(sub) {
            let that = this;
            that.$http({
                method: "GET",
                url:
                    `${JX3BOX.__helperUrl}api/achievement/users/ranking` +
                    (sub ? `?sub=${sub}` : ""),
                headers: { Accept: "application/prs.helper.v2+json" },
                withCredentials: true,
            }).then(
                function(data) {
                    data = data.data;
                    if (data.code === 200) {
                        that.ranks = data.data.ranking;
                    }
                },
                function() {
                    that.ranks = false;
                }
            );
        },
        // 获取成就群
        get_achievement_groups() {
            let that = this;
            that.$http({
                method: "GET",
                url: `${JX3BOX.__helperUrl}api/achievement/groups`,
                headers: { Accept: "application/prs.helper.v2+json" },
                withCredentials: true,
            }).then(
                function(data) {
                    data = data.data;
                    if (data.code === 200) {
                        that.groups = data.data.groups;
                    }
                },
                function() {
                    that.groups = false;
                }
            );
        },
    },
    mounted: function() {
        this.get_achievement_groups();
    },
    watch: {
        active_rank_type: {
            immediate: true,
            handler() {
                this.get_users_ranks(this.active_rank_type);
            },
        },
    },
    filters: {
        resolveAvatarPath: function(val) {
            return val.replace(JX3BOX.__ossRoot, JX3BOX.__ossMirror);
        },
    },
};
</script>

